// WebcamWithZoom.js
import React, { forwardRef } from 'react'
import Webcam from 'react-webcam'
import PropTypes from 'prop-types'
import styled from 'styled-components'

const WebcamRootElement = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
`

const WebcamInnerElement = styled.div`
  overflow: hidden;
  width: 100vw;
  aspect-ratio: 1;
  position: relative;
  flex: 10;
`

const WebcamWithZoom = forwardRef((props, ref) => {
  const zoom = 1

  const videoConstraints = {
    facingMode: 'environment',
    width: { ideal: 1280 * 2 },
    height: { ideal: 1280 * 2 },
    aspectRatio: { ideal: 1 }
  }

  return (
    <WebcamRootElement>
      <WebcamInnerElement>
        <Webcam
          ref={ref}
          screenshotFormat="image/jpeg"
          videoConstraints={videoConstraints}
          style={{
            width: '100vw',
            borderRadius: '1000px',
            aspectRatio: '1',
            position: 'absolute',
            top: 0,
            left: 0,
            transform: `scale(${1 / zoom})`,
            transformOrigin: '0 0'
          }}
        />
        {props.children}
      </WebcamInnerElement>
    </WebcamRootElement>)
})

WebcamWithZoom.displayName = 'WebcamWithZoom'

WebcamWithZoom.propTypes = {
  children: PropTypes.node
}

export default WebcamWithZoom
