// CC attribution: https://stackoverflow.com/questions/37986669/auto-adjust-brightness-contrast-to-read-text-from-images
//

export default async function autoLevels (image, isInMask, lowSqueeze = 0.05, highSqeeze = 0.05) {
  const c = document.createElement('canvas')

  c.width = image.width
  c.height = image.height

  const ctx = c.getContext('2d')

  // draw in image to canvas
  ctx.drawImage(image, 0, 0)

  const idata = ctx.getImageData(0, 0, image.width, image.height)
  const data = idata.data

  let minL = Infinity; let maxL = -Infinity
  const labdata = new Int32Array((data.length / 4) * 3)

  const XC = 100 / 95.047
  const ZC = 100 / 108.883
  const Q = 16 / 116
  const POWT = 1 / 3
  const INV255 = 1 / 255
  const INVXYZSCALE = 1 / 1.055
  const INVXYZSCALE2 = 1 / 12.92

  // convert to lab space
  for (let i = 0, j = 0; i < data.length; i += 4, j++) {
    // Convert RGB to lab
    let r = data[i] * INV255
    let g = data[i + 1] * INV255
    let b = data[i + 2] * INV255

    // assume sRGB
    r = r > 0.04045 ? Math.pow(((r + 0.055) * INVXYZSCALE), 2.4) : (r * INVXYZSCALE2)
    g = g > 0.04045 ? Math.pow(((g + 0.055) * INVXYZSCALE), 2.4) : (g * INVXYZSCALE2)
    b = b > 0.04045 ? Math.pow(((b + 0.055) * INVXYZSCALE), 2.4) : (b * INVXYZSCALE2)

    let x = (r * 0.4124) + (g * 0.3576) + (b * 0.1805)
    let y = (r * 0.2126) + (g * 0.7152) + (b * 0.0722)
    let z = (r * 0.0193) + (g * 0.1192) + (b * 0.9505)

    x *= XC
    z *= ZC

    x = x > 0.008856 ? Math.pow(x, POWT) : (7.787 * x) + Q
    y = y > 0.008856 ? Math.pow(y, POWT) : (7.787 * y) + Q
    z = z > 0.008856 ? Math.pow(z, POWT) : (7.787 * z) + Q

    const l = (116 * y) - 16
    labdata[j * 3] = l // l
    labdata[j * 3 + 1] = 500 * (x - y) // a
    labdata[j * 3 + 2] = 200 * (y - z) // b

    if (!isInMask(j % image.width, Math.floor(j / image.width), image)) continue

    minL = Math.min(minL, l)
    maxL = Math.max(maxL, l)
  }

  // Apply squeeze
  const size = maxL - minL
  minL += size * lowSqueeze
  maxL -= size * highSqeeze

  // Expand to ensure max/min range is at least 32 levels
  const range = maxL - minL
  if (range < 32) {
    const expand = (32 - range) / 2
    minL -= expand
    maxL += expand
  }

  const InvMinL = 1.0 / (maxL - minL)
  const INV_LAB_Y = 1.0 / 116
  const INV_LAB_X = 1.0 / 500
  const INV_LAB_Z = 1.0 / 200
  const C_INV_LAB_2 = 16 / 116.0
  const C_INV_LAB_3 = 1.0 / 7.787

  const XYZ_RGB_POW = 1.0 / 2.4

  for (let i = 0, j = 0; i < data.length; i += 4, j++) {
    const l = (labdata[j * 3] - minL) * InvMinL * maxL
    const aa = labdata[j * 3 + 1]
    const bb = labdata[j * 3 + 2]

    let x
    let y
    let z

    y = (l + 16) * INV_LAB_Y
    x = aa * INV_LAB_X + y
    z = y - bb * INV_LAB_Z

    const y2 = Math.pow(y, 3)
    const x2 = Math.pow(x, 3)
    const z2 = Math.pow(z, 3)
    y = y2 > 0.008856 ? y2 : (y - C_INV_LAB_2) * C_INV_LAB_3
    x = x2 > 0.008856 ? x2 : (x - C_INV_LAB_2) * C_INV_LAB_3
    z = z2 > 0.008856 ? z2 : (z - C_INV_LAB_2) * C_INV_LAB_3

    x *= 0.95047
    z *= 1.08883

    let r = (x * 3.2406) + (y * -1.5372) + (z * -0.4986)
    let g = (x * -0.9689) + (y * 1.8758) + (z * 0.0415)
    let b = (x * 0.0557) + (y * -0.2040) + (z * 1.0570)

    r = r > 0.0031308
      ? ((1.055 * Math.pow(r, XYZ_RGB_POW)) - 0.055)
      : r * 12.92

    g = g > 0.0031308
      ? ((1.055 * Math.pow(g, XYZ_RGB_POW)) - 0.055)
      : g * 12.92

    b = b > 0.0031308
      ? ((1.055 * Math.pow(b, XYZ_RGB_POW)) - 0.055)
      : b * 12.92

    r = Math.min(Math.max(0, r), 1)
    g = Math.min(Math.max(0, g), 1)
    b = Math.min(Math.max(0, b), 1)

    data[i] = r * 255
    data[i + 1] = g * 255
    data[i + 2] = b * 255
  }

  ctx.putImageData(idata, 0, 0)

  const correctedImage = new Image()
  correctedImage.src = c.toDataURL()
  await new Promise(resolve => {
    correctedImage.onload = resolve
  })

  return correctedImage
}
