import React, { useState, useRef } from 'react'
import { createRoot } from 'react-dom/client'
import generateStickerFromVideo, { cancelLastSticker } from './sticker_generation'
import WebcamWithZoom from './WebcamWithZoom'
import styled from 'styled-components'

const Root = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
`

const SnapButton = styled.button`
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  outline-style: none;
  width: 72px;
  height: 72px;
  margin-top: 16px;
  border-radius: 1000px;
  background-color: white;

  &:active {
    background-color: #eee;
  }
`

const StickerHolder = styled.div`
  position: absolute;
  top: calc(50% - sin(45deg) * 50%);
  bottom: calc(50% - sin(45deg) * 50%);
  right: calc(50% - sin(45deg) * 50%);
  left: calc(50% - sin(45deg) * 50%);
  display: flex;
  align-content: center;
  justify-content: center;
  user-select: none;
  z-index: 10;
`

const Sticker = styled.img`
  filter: drop-shadow(10px 10px 3px rgba(0,0,0,0.5));
  max-width: 100%;
  max-height 100%;
  width: 100%;
  object-fit: contain;
`

let runId = 0

const App = () => {
  const webcamRef = useRef(null)
  const [stickerVisible, setStickerVisible] = useState(false)
  const [stickerSrc, setStickerSrc] = useState('')

  const onCapture = async () => {
    const video = webcamRef.current.video
    runId++
    const id = runId

    const stickerImage = await generateStickerFromVideo(video, (cartoonImageSrc) => {
      const image = new Image()
      image.src = cartoonImageSrc

      if (id === runId) {
        setStickerSrc(cartoonImageSrc)
      }
    })

    if (stickerImage) {
      setStickerSrc(stickerImage.src)
      setStickerVisible(true)
    }
  }

  return (
    <Root style={{ backgroundColor: 'rgba(18, 18, 18, 0.1)' }}>
      <WebcamWithZoom ref={webcamRef}>
        <div id="reticle" style={{ position: 'absolute', top: 'calc(50% - 5px)', left: 'calc(50% - 5px)', width: 10, height: 10, borderRadius: 100, backgroundColor: 'rgba(0.5, 0.5, 0.5, 0.33)' }}></div>
        <StickerHolder style={{ display: stickerVisible ? 'flex' : 'none' }} >
          <Sticker src={stickerSrc} onClick={ () => { cancelLastSticker(); setStickerVisible(false) } } />
        </StickerHolder>
      </WebcamWithZoom>
      <SnapButton onClick={onCapture}>&nbsp;</SnapButton>
      <div id="log">&nbsp;</div>
    </Root>
  )
}

const container = document.getElementById('root')
const root = createRoot(container)
root.render(<App />)
