// Find the largest connected region in the mask, starting from the center.
// Thanks GPT-4.
import vtraceWasm from './vtracer/vtracer_webapp_bg.wasm'
import vtraceInit, { BinaryImageConverter } from './vtracer/vtracer_webapp.js'

let vtraceReady = false

vtraceInit(vtraceWasm).then(() => { vtraceReady = true })

// Ensures that the mask is a single connected region.
export function floodMask (mask, width, height) {
  const visited = new Array(width * height).fill(false)

  function isValid (x, y) {
    return x >= 0 && x < width && y >= 0 && y < height
  }

  function bfs (x, y) {
    const queue = [{ x, y }]
    const region = new Set()

    while (queue.length > 0) {
      const { x, y } = queue.shift()
      const index = y * width + x

      if (!isValid(x, y) || visited[index] || mask[index] <= 0.5) continue

      visited[index] = true
      region.add(index)

      for (const [dx, dy] of [
        [0, 1],
        [0, -1],
        [1, 0],
        [-1, 0]
      ]) {
        queue.push({ x: x + dx, y: y + dy })
      }
    }

    return region
  }

  const centerX = Math.floor(width / 2)
  const centerY = Math.floor(height / 2)
  const centerIndex = centerY * width + centerX

  if (mask[centerIndex] <= 0.5) {
    console.error('Center pixel is not in the mask.', centerIndex, mask[centerIndex])
    return
  }

  const largestRegion = bfs(centerX, centerY)

  // Update mask in place
  for (let i = 0; i < mask.length; i++) {
    mask[i] = largestRegion.has(i) ? mask[i] : 0
  }
}

export async function vectorizeBWImage (imageUrl, smoother = false) {
  while (!vtraceReady) { // eslint-disable-line
    await new Promise(resolve => setTimeout(resolve, 100))
  }

  const vtraceCanvas = document.createElement('canvas')
  vtraceCanvas.style.display = 'none'
  vtraceCanvas.id = 'vtraceimage'

  const vtraceSvg = document.createElement('svg')
  vtraceSvg.id = 'vtracesvg'
  vtraceSvg.style.display = 'none'

  document.body.appendChild(vtraceCanvas)
  document.body.appendChild(vtraceSvg)

  const image = new Image()
  image.src = imageUrl
  await new Promise(resolve => {
    image.onload = () => {
      vtraceCanvas.width = image.width
      vtraceCanvas.height = image.height
      vtraceSvg.setAttribute('viewBox', `0 0 ${image.width} ${image.height}`)

      const ctx = vtraceCanvas.getContext('2d')
      ctx.drawImage(image, 0, 0)
      resolve()
    }
  })

  const vtrace = BinaryImageConverter.new_with_string(
    JSON.stringify({
      canvas_id: 'vtraceimage',
      svg_id: 'vtracesvg',
      mode: 'spline',
      clustering_mode: 'binary',
      hierarchical: 'cutout',
      corner_threshold: smoother ? 3.14 : 1.0646, // deg2rad(globalcorner),
      length_threshold: 3.5,
      max_iterations: 10,
      splice_threshold: smoother ? 3.14 : 1.308, // deg2rad(globalsplice),
      filter_speckle: 256,
      color_precision: 2,
      layer_difference: 16,
      path_precision: 8
    }))

  vtrace.init()

  return await new Promise(resolve => {
    const vtraceInterval = setInterval(function tick () {
      const progress = vtrace.progress()
      vtrace.tick()

      if (progress === 100) {
        clearInterval(vtraceInterval)
        const svg = document.getElementById('vtracesvg').innerHTML
        vtrace.free()
        vtraceCanvas.remove()
        vtraceSvg.remove()
        resolve(svg)
      }
    }, 1)
  })
}

export async function applyLUT (image, lut) {
  const imageCanvas = document.createElement('canvas')
  imageCanvas.width = image.naturalWidth
  imageCanvas.height = image.naturalHeight

  const lutCanvas = document.createElement('canvas')
  lutCanvas.width = lut.naturalWidth
  lutCanvas.height = lut.naturalHeight

  const imageContext = imageCanvas.getContext('2d')
  const lutContext = lutCanvas.getContext('2d')

  imageContext.clearRect(0, 0, image.naturalWidth, image.naturalHeight)
  imageContext.drawImage(image, 0, 0)
  const imageData = imageContext.getImageData(0, 0, image.naturalWidth, image.naturalHeight)
  const iData = imageData.data

  lutContext.clearRect(0, 0, lut.naturalWidth, lut.naturalHeight)
  lutContext.drawImage(lut, 0, 0)
  const lutData = lutContext.getImageData(0, 0, lut.naturalWidth, lut.naturalHeight)
  const lData = lutData.data

  for (let i = 0, l = iData.length; i < l; i += 4) {
    const r = Math.floor(iData[i] / 4)
    const g = Math.floor(iData[i + 1] / 4)
    const b = Math.floor(iData[i + 2] / 4)

    const lutX = (b % 8) * 64 + r
    const lutY = Math.floor(b / 8) * 64 + g
    const lutIndex = (lutY * lut.naturalWidth + lutX) * 4

    const lutR = lData[lutIndex]
    const lutG = lData[lutIndex + 1]
    const lutB = lData[lutIndex + 2]

    imageData.data[i] = lutR
    imageData.data[i + 1] = lutG
    imageData.data[i + 2] = lutB
    imageData.data[i + 3] = 255
  }

  imageContext.clearRect(0, 0, image.naturalWidth, image.naturalHeight)
  imageContext.putImageData(imageData, 0, 0)

  const correctedImage = new Image()
  correctedImage.src = imageCanvas.toDataURL()
  await new Promise(resolve => {
    correctedImage.onload = resolve
  })

  return correctedImage
}

export async function videoToImage (video, width, height) {
  const canvas = document.createElement('canvas')
  canvas.width = width
  canvas.height = height
  const ctx = canvas.getContext('2d')
  ctx.imageSmoothingEnabled = true
  ctx.imageSmoothingQuality = 'high'
  ctx.drawImage(
    video,
    0,
    0,
    video.videoWidth,
    video.videoHeight,
    0,
    0,
    width,
    height
  )
  const img = new Image()
  await new Promise(resolve => {
    img.onload = resolve
    img.src = canvas.toDataURL()
  })

  return img
}

export async function dataURLToImage (dataUrl) {
  return new Promise((resolve, reject) => {
    const img = new Image()
    img.onload = () => resolve(img)
    img.onerror = reject
    img.src = dataUrl
  })
}

export async function canvasToImage (canvas) {
  return await dataURLToImage(canvas.toDataURL('image/png'))
}

export function getImageData (image) {
  const canvas = document.createElement('canvas')
  canvas.width = image.naturalWidth
  canvas.height = image.naturalHeight
  const ctx = canvas.getContext('2d')
  ctx.drawImage(image, 0, 0)
  return ctx.getImageData(0, 0, image.naturalWidth, image.naturalHeight)
}

export async function resizeImage (image, width, height) {
  const canvas = document.createElement('canvas')
  canvas.width = width
  canvas.height = height
  const ctx = canvas.getContext('2d')
  ctx.drawImage(image, 0, 0, width, height)
  const resizedImage = new Image()
  resizedImage.src = canvas.toDataURL()
  await new Promise(resolve => {
    resizedImage.onload = resolve
  })

  return resizedImage
}
