import ort from 'onnxruntime-web'

export async function imageToChannelInputTensor (image, nchannels) {
  return new Promise((resolve) => {
    const canvas = document.createElement('canvas')
    canvas.width = image.width
    canvas.height = image.height
    const ctx = canvas.getContext('2d')
    ctx.drawImage(image, 0, 0, image.width, image.height)

    const imageData = ctx.getImageData(0, 0, image.width, image.height)
    const { data, width, height } = imageData

    // Normalize the pixel values to the range [0, 1] and re-arrange the dimensions to
    // be [C, H, W] as per torchvision format, which RITM was trained on. Input data is arranged by width then height so needs to be reverse read.
    const inputTensor = new Float32Array(nchannels * width * height)

    for (let x = 0; x < width; x++) {
      for (let y = 0; y < height; y++) {
        const dataOffset = (y * width + x) * 4
        const wh = width * height
        const tensorOffset = y * width + x
        inputTensor[tensorOffset] = data[dataOffset] / 255
        inputTensor[wh + tensorOffset] = data[dataOffset + 1] / 255
        inputTensor[2 * wh + tensorOffset] = data[dataOffset + 2] / 255
      }
    }

    resolve(new ort.Tensor('float32', inputTensor, [1, nchannels, height, width]))
  })
}

export async function imageToRitmInputTensor (image) {
  return imageToChannelInputTensor(image, 4)
}

export async function imageToContourInputTensor (image) {
  return imageToChannelInputTensor(image, 3)
}

export async function imageToCartoonizeInputTensor (image) {
  const canvas = document.createElement('canvas')
  canvas.width = image.width
  canvas.height = image.height
  const ctx = canvas.getContext('2d')
  ctx.drawImage(image, 0, 0, image.width, image.height)

  const imageData = ctx.getImageData(0, 0, image.width, image.height)
  const { data, width, height } = imageData

  // Normalize the pixel values to the range [0, 1] and re-arrange the dimensions to
  // be [W, H, C]
  const inputTensor = new Float32Array(3 * width * height)

  const getPixelAt = (x, y, c) => {
    return (data[(y * width + x) * 4 + c] / 127.5) - 1
  }

  for (let x = 0; x < width; x++) {
    for (let y = 0; y < height; y++) {
      inputTensor[y * width * 3 + x * 3 + 0] = getPixelAt(x, y, 0)
      inputTensor[y * width * 3 + x * 3 + 1] = getPixelAt(x, y, 1)
      inputTensor[y * width * 3 + x * 3 + 2] = getPixelAt(x, y, 2)
    }
  }

  return new ort.Tensor('float32', inputTensor, [1, width, height, 3])
}

export async function loadModel (modelPath) {
  const session = await ort.InferenceSession.create(modelPath)
  return session
}

// Load JSON tensors
export async function loadJSON (url) {
  const response = await fetch(url)
  const data = await response.json()
  return data
}

export function jsonToTensor (json, type) {
  const shape = [json.length, ...json[0].length ? [json[0].length] : [], ...json[0][0].length ? [json[0][0].length] : [], ...json[0][0][0].length ? [json[0][0][0].length] : []]
  const flattened = json.flat(Infinity)
  return new ort.Tensor(type, flattened, shape)
}
